import { BrowserRouter as Router, Switch, Route } from "react-router-dom";
import Notes from "./pages/Notes";
import Create from "./pages/Create";
import { ThemeProvider } from "@material-ui/core";
import { createTheme } from "@material-ui/core/styles";
import { green } from "@material-ui/core/colors";
import Layout from "./components/Layout";
import { useState } from "react";

function App() {
  const [notes, setNotes] = useState([
    {
      title: "Yoshi's birthday bash",
      details:
        "Lorem Ipsum is simply dummy text of the printing and typesetting industry. Lorem Ipsum has been the industry's standard dummy text ever since the 1500s, when an unknown printer took a galley of type and scrambled it to make a type specimen book.",
      category: "reminders",
      id: 1,
    },
    {
      title: "Complete my ninja training",
      details:
        "Lorem Ipsum is simply dummy text of the printing and typesetting industry. Lorem Ipsum has been the industry's standard dummy text ever since the 1500s, when an unknown printer took.",
      category: "work",
      id: 2,
    },
  ]);

  const handleDelete = (id) => {
    const newNotes = notes.filter((note) => note.id !== id);
    setNotes(newNotes);
  };

  const addNote = (newNote) => {
    const newNotes = [...notes, newNote];
    setNotes(newNotes);
  };

  const [toggleDark, settoggleDark] = useState(false);
  const myTheme = createTheme({
    // Theme settings
    palette: {
      type: toggleDark ? "dark" : "light",
      primary: green,
      secondary: green,
    },
    typography: {
      fontFamily: "Quicksand",
      fontWeightLight: 400,
      fontWeightRegular: 500,
      fontWeightMedium: 600,
      fontWeightBold: 700,
    },
  });

  return (
    <ThemeProvider theme={myTheme}>
      <Router>
        <Layout toggleDark={toggleDark} settoggleDark={settoggleDark}>
          <Switch>
            <Route exact path="/op-react-whereissw">
              <Notes
                handleDelete={handleDelete}
                notes={notes}
                setNotes={setNotes}
              />
            </Route>
            <Route exact path="/op-react-whereissw/create">
              <Create addNote={addNote} />
            </Route>
          </Switch>
        </Layout>
      </Router>
    </ThemeProvider>
  );
}

export default App;
